<template>
	<div id="errander-store-home">
		<public-header :title="store.title"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="group margin-10-t">
				<van-row gutter="20">
					<van-col span="8" v-if="0">
						<router-link tag="div" :to="util.getUrl({path: '/'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-people"></i>
							</div>
							<div class="bottom">
								<span>店员列表</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/plugin/pages/paotui/store/finance/yucunjin'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-order c-info"></i>
							</div>
							<div class="bottom">
								<span>预存金明细</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/plugin/pages/paotui/store/finance/recharge'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-recharge c-info"></i>
							</div>
							<div class="bottom">
								<span>预存金充值</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				store: {},
				showPreLoading: true,
				menufooter: {}
			}
		},
		components: {
			publicHeader,
			publicFooter
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'errander/store/info',
					data: {
						menufooter: 1,
					},
					success: (res) => {
						this.store = this.util.extend(this.store, res.store);
					}
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#errander-store-home .content .group{
		padding: 0 10px;
		background: #fff;
	}
	#errander-store-home .content .group .van-row{
		text-align: center;
	}
	#errander-store-home .content .group .merchant-item{
		margin-bottom: 20px;
		position: relative;
	}
	#errander-store-home .content .group .merchant-item .top{
		padding: 10px;
	}
	#errander-store-home .content .group .merchant-item .top .icon{
		font-size: 40px;
	}
	#errander-store-home .content .group .merchant-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
	#errander-store-home .content .group .merchant-item .top .icon-shop{
		color:#F09A91;
	}
	#errander-store-home .content .group .merchant-item .top .icon-goods{
		color:#E9972D;
	}
	#errander-store-home .content .group .merchant-item .top .icon-recharge{
		color:#E9972D;
	}
	#errander-store-home .content .group .merchant-item .top .icon-medal{
		color:#FFD700;
	}
	#errander-store-home .content .group .merchant-item .message-num{
		position: absolute;
		left: 55%;
		top: 0;
		padding: 2px 3px;
		min-width: 16px;
		text-align: center;
		background-color: #33aafc;
		color: #fff;
		font-size: 12px;
		border-radius: 50px;
	}
</style>


